import Image from "next/image";
import { MapPin, BadgeCheck, MessageCircle, Instagram, Video } from "lucide-react"; // Video icon for TikTok
import { Store } from "@/types";

export default function StoreHeader({ store }: { store: Store }) {
  return (
    <div className="bg-white shadow-sm pb-4 rounded-b-3xl relative z-10">
      
      {/* 1. HERO IMAGE (Cover Photo) */}
      <div className="h-32 md:h-48 w-full relative bg-gray-900 rounded-b-2xl overflow-hidden">
        {store.cover_image_url ? (
          <Image 
            src={store.cover_image_url} 
            alt="Cover" 
            fill 
            className="object-cover opacity-90"
          />
        ) : (
          <div className="w-full h-full bg-gradient-to-r from-gray-800 to-gray-900" />
        )}
      </div>
      
      <div className="px-5 -mt-10 relative">
        <div className="flex justify-between items-end">
          {/* 2. PROFILE LOGO */}
          <div className="w-24 h-24 bg-white rounded-full p-1.5 shadow-xl">
            <div className="w-full h-full bg-gray-100 rounded-full overflow-hidden relative">
              {store.logo_url ? (
                <Image src={store.logo_url} alt={store.name} fill className="object-cover" />
              ) : (
                <div className="flex items-center justify-center h-full text-2xl font-bold text-gray-400">
                  {store.name.charAt(0)}
                </div>
              )}
            </div>
          </div>
          
          {/* 3. SOCIAL ACTIONS */}
          <div className="flex gap-2 mb-2">
            {store.instagram_url && (
              <a href={store.instagram_url} target="_blank" className="p-2 bg-gray-100 rounded-full text-pink-600 hover:bg-pink-50 transition">
                <Instagram size={20} />
              </a>
            )}
            {store.tiktok_url && (
              <a href={store.tiktok_url} target="_blank" className="p-2 bg-gray-100 rounded-full text-black hover:bg-gray-200 transition">
                <Video size={20} />
              </a>
            )}
            <a 
              href={`https://wa.me/${store.whatsapp_number}`} 
              target="_blank"
              className="bg-green-600 text-white px-4 py-2 rounded-full text-sm font-bold shadow-md flex items-center gap-2 hover:bg-green-700 transition"
            >
              <MessageCircle size={16} /> Chat
            </a>
          </div>
        </div>

        <div className="mt-3">
            <div className="flex items-center gap-1">
              <h1 className="text-2xl font-bold text-gray-900">{store.name}</h1>
              <BadgeCheck className="text-blue-500 fill-blue-50 w-6 h-6" />
            </div>
            <p className="text-gray-500 text-sm flex items-center gap-1 mt-1">
              <MapPin size={14} /> {store.location} â€¢ {store.category}
            </p>
            {store.description && (
              <p className="text-sm text-gray-600 mt-2 max-w-lg">{store.description}</p>
            )}
        </div>
      </div>
    </div>
  );
}