"use client"; 

import { useState } from "react";
import Image from "next/image";
import { Search, ShoppingBag, Plus, X, ChevronLeft, ChevronRight } from "lucide-react";
import { Store, Product } from "@/types";

import StoreHeader from "./store/StoreHeader";
import CheckoutModal from "./store/CheckoutModal";

interface StoreFrontProps {
  store: Store;
  products: Product[];
}

export default function StoreFront({ store, products }: StoreFrontProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [cart, setCart] = useState<{ product: Product; qty: number }[]>([]);
  const [isCheckoutOpen, setIsCheckoutOpen] = useState(false);
  
  // Product Modal State
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [activeImageIndex, setActiveImageIndex] = useState(0);

  const filteredProducts = products.filter((p) =>
    p.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const addToCart = (product: Product, e?: React.MouseEvent) => {
    if (e) e.stopPropagation();
    setCart((prev) => {
      const existing = prev.find((item) => item.product.id === product.id);
      if (existing) {
        return prev.map((item) =>
          item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item
        );
      }
      return [...prev, { product, qty: 1 }];
    });
    // Don't close modal, just show success feedback (optional) or leave as is
  };

  const removeFromCart = (id: string) => {
    setCart(prev => prev.filter(item => item.product.id !== id));
  };

  const cartCount = cart.reduce((sum, item) => sum + item.qty, 0);
  const cartTotal = cart.reduce((sum, item) => sum + item.product.price * item.qty, 0);

  const openProductModal = (product: Product) => {
    setSelectedProduct(product);
    setActiveImageIndex(0);
  };

  return (
    <div className="bg-gray-50 min-h-screen font-sans pb-32">
      
      <StoreHeader store={store} />

      {/* --- SEARCH & CART --- */}
      <div className="px-5 mt-6 mb-4 flex gap-3">
        <div className="relative flex-1">
          <Search className="absolute left-3 top-3 text-gray-400 w-5 h-5" />
          <input 
            type="text" 
            placeholder="Search products..."
            className="w-full bg-white pl-10 pr-4 py-3 rounded-xl text-sm shadow-sm border border-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-900"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        <button 
          onClick={() => setIsCheckoutOpen(true)}
          className="bg-gray-900 text-white p-3 rounded-xl shadow-lg relative"
        >
          <ShoppingBag size={20} />
          {cartCount > 0 && (
            <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-5 w-5 flex items-center justify-center rounded-full border-2 border-white">
              {cartCount}
            </span>
          )}
        </button>
      </div>

      {/* --- PRODUCT GRID --- */}
      <main className="px-4">
        {filteredProducts.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {filteredProducts.map((product) => (
              <div 
                key={product.id} 
                onClick={() => openProductModal(product)}
                className="bg-white p-2 rounded-2xl shadow-sm border border-gray-100 active:scale-95 transition cursor-pointer hover:shadow-md relative group"
              >
                <div className="aspect-square bg-gray-100 rounded-xl mb-3 relative overflow-hidden">
                   {product.image_urls && product.image_urls.length > 0 ? (
                     <Image src={product.image_urls[0]} alt={product.name} fill className="object-cover" />
                   ) : (
                     <div className="w-full h-full flex items-center justify-center text-gray-300">
                        <ShoppingBag className="opacity-20" />
                     </div>
                   )}
                   <button 
                     onClick={(e) => addToCart(product, e)}
                     className="absolute bottom-2 right-2 bg-white text-gray-900 p-2 rounded-full shadow-lg hover:bg-emerald-50 active:scale-90 transition z-10 opacity-100 md:opacity-0 md:group-hover:opacity-100"
                   >
                     <Plus size={16} strokeWidth={3} />
                   </button>
                </div>
                <div className="px-1 mb-2">
                   <h3 className="font-semibold text-gray-900 text-sm truncate">{product.name}</h3>
                   {/* TRUNCATE DESCRIPTION IN GRID so it doesn't break layout */}
                   <p className="text-emerald-700 font-bold text-sm">â‚¦{product.price.toLocaleString()}</p>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-20 text-gray-400">
             <p>No items found.</p>
          </div>
        )}
      </main>

      {/* --- STICKY CART --- */}
      {cart.length > 0 && !isCheckoutOpen && (
        <div className="fixed bottom-5 left-4 right-4 bg-gray-900 text-white p-4 rounded-2xl shadow-2xl flex justify-between items-center z-40 animate-in slide-in-from-bottom-5 cursor-pointer" onClick={() => setIsCheckoutOpen(true)}>
           <div className="flex flex-col">
              <span className="text-xs text-gray-400">{cartCount} Items</span>
              <span className="font-bold text-lg">â‚¦{cartTotal.toLocaleString()}</span>
           </div>
           <span className="bg-white text-gray-900 px-6 py-2 rounded-xl font-bold text-sm hover:bg-gray-100 transition">
             Checkout
           </span>
        </div>
      )}

      <CheckoutModal 
        isOpen={isCheckoutOpen} 
        onClose={() => setIsCheckoutOpen(false)} 
        cart={cart} 
        store={store}
        onRemoveItem={removeFromCart}
      />

     {/* --- PREMIUM PRODUCT MODAL (Fixed Desktop Scroll) --- */}
      {selectedProduct && (
        <div className="fixed inset-0 z-50 flex items-end md:items-center justify-center bg-black/70 backdrop-blur-sm p-0 md:p-4 animate-in fade-in">
          
          {/* MODAL CONTAINER 
             - Mobile: h-[90vh] (90% height)
             - Desktop: md:h-[80vh] (Fixed 80% height - CRITICAL for scrolling)
             - Flex layout to split image and text
          */}
          <div className="bg-white w-full md:max-w-4xl h-[90vh] md:h-[80vh] rounded-t-3xl md:rounded-3xl shadow-2xl flex flex-col md:flex-row relative overflow-hidden">
            
            {/* Close Button */}
            <button 
              onClick={() => setSelectedProduct(null)} 
              className="absolute top-4 right-4 z-30 bg-white/80 hover:bg-white p-2 rounded-full shadow-sm backdrop-blur-md transition"
            >
              <X size={20} />
            </button>

            {/* LEFT SIDE: Image Gallery */}
            {/* Mobile: h-72 fixed. Desktop: h-full (matches container height) */}
            <div className="w-full md:w-1/2 bg-gray-100 relative h-72 md:h-full shrink-0">
               {selectedProduct.image_urls && selectedProduct.image_urls.length > 0 ? (
                 <Image 
                   src={selectedProduct.image_urls[activeImageIndex]} 
                   alt="" 
                   fill 
                   className="object-cover" 
                 />
               ) : (
                 <div className="w-full h-full flex items-center justify-center text-gray-300"><ShoppingBag size={48} /></div>
               )}

               {/* Dots for Mobile Carousel */}
               {selectedProduct.image_urls && selectedProduct.image_urls.length > 1 && (
                 <div className="absolute bottom-4 left-0 right-0 flex justify-center gap-2 z-20">
                   {selectedProduct.image_urls.map((_, idx) => (
                     <button 
                       key={idx}
                       onClick={() => setActiveImageIndex(idx)}
                       className={`w-2 h-2 rounded-full transition-all shadow-sm ${activeImageIndex === idx ? 'bg-white w-4' : 'bg-white/50'}`}
                     />
                   ))}
                 </div>
               )}
            </div>

            {/* RIGHT SIDE: Details */}
            {/* h-full ensures it fills the 80vh container, flex-col stacks content */}
            <div className="w-full md:w-1/2 flex flex-col h-full bg-white overflow-hidden">
               
               {/* 1. SCROLLABLE CONTENT AREA */}
               {/* flex-1 tells this part to take all space but allow shrinking/scrolling */}
               <div className="flex-1 overflow-y-auto p-6 md:p-8">
                  
                  <h2 className="text-2xl md:text-3xl font-bold text-gray-900 leading-tight">{selectedProduct.name}</h2>
                  
                  <div className="mt-2 flex items-center gap-2">
                     <p className="text-2xl font-bold text-emerald-600">â‚¦{selectedProduct.price.toLocaleString()}</p>
                     {selectedProduct.stock_quantity > 0 ? (
                       <span className="bg-green-100 text-green-700 text-xs px-2 py-1 rounded-full font-bold">In Stock</span>
                     ) : (
                       <span className="bg-red-100 text-red-600 text-xs px-2 py-1 rounded-full font-bold">Out of Stock</span>
                     )}
                  </div>

                  {/* Desktop Thumbnail Row */}
                  {selectedProduct.image_urls && selectedProduct.image_urls.length > 1 && (
                    <div className="hidden md:flex gap-2 mt-6 overflow-x-auto pb-2 scrollbar-hide">
                      {selectedProduct.image_urls.map((url, idx) => (
                        <button 
                          key={idx}
                          onClick={() => setActiveImageIndex(idx)}
                          className={`relative w-16 h-16 rounded-lg overflow-hidden border-2 flex-shrink-0 ${activeImageIndex === idx ? 'border-gray-900' : 'border-transparent opacity-60'}`}
                        >
                          <Image src={url} alt="" fill className="object-cover" />
                        </button>
                      ))}
                    </div>
                  )}

                  {/* Description */}
                  <div className="mt-6 border-t border-gray-100 pt-6">
                    <h3 className="text-sm font-bold text-gray-900 uppercase tracking-wide mb-2">Description</h3>
                    <div className="prose prose-sm text-gray-500 leading-relaxed pb-4">
                      {selectedProduct.description || "No description available."}
                    </div>
                  </div>
               </div>

               {/* 2. FIXED FOOTER */}
               {/* shrink-0 prevents this from disappearing when text is long */}
               <div className="p-4 md:p-6 border-t border-gray-100 bg-white z-20 shrink-0">
                  <button 
                    onClick={() => addToCart(selectedProduct)} 
                    disabled={selectedProduct.stock_quantity < 1}
                    className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold shadow-lg hover:bg-gray-800 active:scale-95 transition flex items-center justify-center gap-2 disabled:bg-gray-300 disabled:cursor-not-allowed"
                  >
                    {selectedProduct.stock_quantity > 0 ? (
                      <>
                        <ShoppingBag size={20} /> Add to Bag
                      </>
                    ) : (
                      "Sold Out"
                    )}
                  </button>
               </div>

            </div>
          </div>
        </div>
      )}

    </div>
  );
}