import { useState } from "react";
import Image from "next/image";
import { X, Trash2, MessageCircle } from "lucide-react";
import { Store, Product } from "@/types";

interface CheckoutModalProps {
  isOpen: boolean;
  onClose: () => void;
  cart: { product: Product; qty: number }[];
  store: Store;
  onRemoveItem: (id: string) => void;
}

export default function CheckoutModal({ isOpen, onClose, cart, store, onRemoveItem }: CheckoutModalProps) {
  const [customer, setCustomer] = useState({ name: "", phone: "", address: "", email: "" });

  if (!isOpen) return null;

  const cartTotal = cart.reduce((sum, item) => sum + item.product.price * item.qty, 0);

  // --- HELPER: Format Phone Number to 234 ---
  const formatPhoneNumber = (phone: string) => {
    let clean = phone.replace(/\D/g, ''); // Remove non-numbers
    if (clean.startsWith('0')) clean = clean.substring(1); // Remove leading 0
    if (!clean.startsWith('234')) clean = '234' + clean;   // Add 234
    return clean;
  };

  const handleWhatsAppCheckout = (e: React.FormEvent) => {
    e.preventDefault();

    // 1. Prepare Data
    const orderItems = cart
      .map((item) => `- ${item.qty}x ${item.product.name} (â‚¦${(item.product.price * item.qty).toLocaleString()})`)
      .join("\n");

    const finalPhone = formatPhoneNumber(customer.phone);
    
    // 2. The Message (With your custom text)
    const message = `Hello ${store.name}! ðŸ‘‹\nI want to place an order:\n\n${orderItems}\n\n*Total: â‚¦${cartTotal.toLocaleString()}*\n\n*Customer Details:*\nName: ${customer.name}\nPhone: ${finalPhone}\nAddress: ${customer.address}\nEmail: ${customer.email}\n\nKindly provide me with the account details to make payments and expected delivery time and date...`;

    // 3. Vendor Number Logic (Ensure vendor number is also clean)
    const vendorNumber = formatPhoneNumber(store.whatsapp_number);
    
    // 4. Redirect
    const whatsappUrl = `https://wa.me/${vendorNumber}?text=${encodeURIComponent(message)}`;
    window.open(whatsappUrl, "_blank");
  };

  return (
    <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
      <div className="bg-white w-full max-w-md rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10 flex flex-col max-h-[90vh]">
        
        {/* Header */}
        <div className="p-5 border-b border-gray-100 flex justify-between items-center bg-gray-50">
          <h2 className="font-bold text-lg text-gray-900">Your Bag ({cart.length})</h2>
          <button onClick={onClose} className="p-2 bg-white rounded-full shadow-sm text-gray-500">
            <X size={20} />
          </button>
        </div>

        {/* Cart Items */}
        <div className="p-5 overflow-y-auto flex-1">
          {cart.map((item) => (
            <div key={item.product.id} className="flex justify-between items-center mb-4">
              <div className="flex gap-3">
                <div className="h-12 w-12 bg-gray-100 rounded-lg relative overflow-hidden">
                  {item.product.image_url && <Image src={item.product.image_url} alt="" fill className="object-cover" />}
                </div>
                <div>
                  <p className="font-medium text-sm text-gray-900">{item.product.name}</p>
                  <p className="text-xs text-gray-500">{item.qty} x â‚¦{item.product.price.toLocaleString()}</p>
                </div>
              </div>
              <button onClick={() => onRemoveItem(item.product.id)} className="text-red-400 p-2">
                <Trash2 size={16} />
              </button>
            </div>
          ))}
          <div className="border-t border-gray-100 pt-4 mt-4 flex justify-between items-center">
            <span className="font-bold text-gray-900">Total</span>
            <span className="font-bold text-xl text-emerald-600">â‚¦{cartTotal.toLocaleString()}</span>
          </div>
        </div>

        {/* Form */}
        <div className="p-5 bg-gray-50 border-t border-gray-100">
          <h3 className="text-sm font-bold text-gray-900 mb-3">Delivery Details</h3>
          <form onSubmit={handleWhatsAppCheckout} className="space-y-3">
            <input 
              required placeholder="Your Name" 
              className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
              value={customer.name} onChange={e => setCustomer({...customer, name: e.target.value})}
            />
            <input 
              required placeholder="Phone (e.g. 080...)" type="tel"
              className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
              value={customer.phone} onChange={e => setCustomer({...customer, phone: e.target.value})}
            />
            {/* ADDED EMAIL FIELD */}
            <input 
              required placeholder="Email Address" type="email"
              className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
              value={customer.email} onChange={e => setCustomer({...customer, email: e.target.value})}
            />
            <input 
              required placeholder="Delivery Address" 
              className="w-full p-3 rounded-xl border border-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-gray-900"
              value={customer.address} onChange={e => setCustomer({...customer, address: e.target.value})}
            />
            
            <button 
              type="submit" disabled={cart.length === 0}
              className="w-full bg-green-600 text-white py-4 rounded-xl font-bold shadow-lg hover:bg-green-700 active:scale-95 transition mt-2 flex items-center justify-center gap-2"
            >
              <MessageCircle size={20} />
              Order on WhatsApp
            </button>
          </form>
        </div>

      </div>
    </div>
  );
}